<template>
  <div>
    <div class="div-backend-top">
      <div class="text-end mt-4 mx-5"> 
        <button 
          type="button" 
          class="btn btn-secondary"
          @click="openModal(true)"
        >
          
        </button>
      </div>
      <div class="px-3">
        <table class="table table-hover mt-4">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th width="120"></th>
              <th width="120"></th>
              <th width="120"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="article in articles" :key="article.id">
              <td>{{ article.title }}</td>
              <td>{{ article.author }}</td>
              <td>{{ article.description }}</td>
              <td>{{ $filters.date(article.create_at) }}</td>
              <td>
                <span class="text-success" v-if="article.isPublic"></span>
                <span v-else></span>
              </td>
              <td>
                <button 
                  type="button" 
                  class="btn btn-sm btn-outline-secondary border-end-0 rounded-0"
                  @click="getArticle(article.id)"
                >
                  
                </button>
                <button 
                  type="button" 
                  class="btn btn-sm btn-outline-danger rounded-0"
                  @click="openDelArticleModal(article)"
                >
                  
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <ArticleModal :article="tempArticle" :is-new="isNew" ref="articleModal" @update-article="updateArticle"/>
    <DeleteProduct :item="tempArticle" @del-product="delArticle" ref="deleteProduct"/>
  </div>
</template>

<script>
import ArticleModal from '@/components/Backend/ArticleModal.vue';
import DeleteProduct from '@/components/Backend/DeleteProduct.vue';

export default {
  inject: ['emitter', '$httpMessageState'],
  components: {
    ArticleModal,
    DeleteProduct,
  },
  data() {
    return {
      articles: [],
      article: {},
      isPublic: false,
      isNew: false,
      tempArticle: {},
      currentPage: 1,
    };
  },
  mounted() {
    this.getArticles();
  },
  methods: {
    getArticles(page = 1) {
      this.currentPage = page;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/articles?page=${page}`;
      this.$http.get(api)
      .then((response)=>{
        this.articles = response.data.articles;
        this.pagination = response.data.pagination;
      })
      .catch((error)=>{
        this.$httpMessageState(error.response, '');
      })
    },
    getArticle(id) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/article/${id}`;
      this.$http.get(api)
      .then((response)=>{
        if(response.data.success) {
          this.openModal(false, response.data.article);
          this.isNew = false;
        }
      })
      .catch((error) => {
        this.$httpMessageState(error.response, '');
      })
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempArticle = {
          isPublic: false,
          create_at: new Date().getTime() / 1000,
          tag: [],
        };
        this.isNew = true;
      } else {
        this.tempArticle = { ...item };
        this.isNew = false;
      }
      this.$refs.articleModal.openModal();
    },
    updateArticle(item) {
      this.tempArticle = item;
      let api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/article`;
      let httpMethod = 'post';
      let status = '';
      
      if(!this.isNew) {
        api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/article/${this.tempArticle.id}`;
        httpMethod = 'put';
        status = ''
      }
      const articleComponent = this.$refs.articleModal;
      this.$http[httpMethod](api, { data: this.tempArticle })
      .then((response)=>{
        this.$httpMessageState(response, status);
        articleComponent.hideModal();
        this.getArticles(this.currentPage);
      })
      .catch((error) => {
        this.$httpMessageState(error.response, '');
      })
    },
    openDelArticleModal(item) {
      this.tempArticle = { ...item };
      const delComponent = this.$refs.deleteProduct;
      delComponent.openModal();
    },
    delArticle() {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/article/${this.tempArticle.id}`;
      this.$http.delete(api)
      .then((response)=>{
        this.$httpMessageState(response, '');
        const delComponent = this.$refs.deleteProduct;
        delComponent.hideModal();
        this.getArticle(this.currentPage);
      })
      .catch((error)=>{
        this.$httpMessageState(error.response, '');
      });
    },
  },
}
</script>